@mixin switch {
    .switch {
        /* 宽度 */
        --widht: 2em;
        --height: 1em;

        margin: 0;
        margin-right: 3px;
        appearance: none;
        position: relative;
        width: var(--widht);
        height: var(--height);

        &::before {
            content: "";
            width: inherit;
            height: inherit;
            position: absolute;
            background-color: #808080;
            border-radius: 50em;
        }

        &::after {
            content: "";
            width: var(--height);
            height: var(--height);
            border-radius: 50em;
            background-color: #fff;
            position: absolute;
            top: 0;
            left: 0;
            transition: .2s linear;
        }

        &:checked::before {
            background-color: #05c46b;
        }

        &:checked::after {
            left: calc(var(--widht) - var(--height));
        }

    }
}